@import "../../variables.scss";


.brewCardBack {
    padding: $base-spacing * 4;
    position: relative;

    h2,
    h3 {
      color: $primary-text;
    }

    h2 {
        font-size: 1.3rem;
        text-overflow: ellipsis;
        max-width: 90%;
        margin-bottom: $base-spacing * 2;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    p{
      color: $tertiary-text;
    }
      h3 {
        font-size: 1.1rem;
        margin-bottom: $base-spacing;
      }
      .list {
        height: 200px;
        display:flex;
      }
      ul {
        list-style-type: none;
        list-style-position: inside;
        height: 100%;
        width: 100%;
        overflow-x: hidden;
label{
  margin-left: -10px;
  flex-direction: row;
  justify-content: flex-start;
  color: $primary-text;
}
      li {
        margin: $base-spacing 0;
        color: $tertiary-text;
}
      }
    }
    