.background {
    height: 100%;
    width: 100%;
    background-color: linear-gradient(
        rgba(202, 199, 199, 0.3),
        rgba(202, 199, 199, 0.3)
      );
    padding: 20px;
  
    .feedback {
      height: 200px;
      width: 300px;
      background-color: white;
      border: black;
      border-radius: 15px;
  
      h2 {
        color: orange;
        text-align: center;
        margin-top: 32px;
        font-size: 1.7rem;
      }
  
      p {
        color: green;
        text-align: center;
        padding: 2em;
      }
    }
  }