@import "../../variables.scss";

.CardList {
    max-width: 100vw;
    display: grid;
    grid-template-columns: repeat(auto-fill, 300px);
    grid-gap: $base-spacing * 4;
    justify-content: space-around;
    min-height: 100%;
    padding: $base-spacing * 4;
    
    .card {
        height: 550px;
        width: 300px;
      }
    
}

