$base-spacing: 5px;

$primary-color: rgb(28, 7, 221);
$secondary-color: rgb(245, 0, 0);
$tertiary-color: rgb(141, 140, 140);

$primary-color-transparent: rgba(28, 7, 221, 0.85);
$secondary-color-transparent: rgba(245, 0, 0, 0.85);
$tertiary-color-transparent: rgba(141, 140, 140, 0.85);

$primary-border: solid 2px $primary-color;
$secondary-border: solid 2px $secondary-color;

$primary-text: rgba(245, 0, 0, 0.85);
$secondary-text: black;
$tertiary-text: black;

$card-shadowing: 0 4px 7px rgba(0, 0, 0, 0.2), 0 4px 7px rgba(0, 0, 0, 0.27);
$card-hover-shadowing: 0 14px 28px rgba(0, 0, 0, 0.27),
  0 10px 10px rgba(0, 0, 0, 0.3);

$NavBar-height-mb: 60px;
$NavBar-height: 75px;

$border-rounding: 5px;

%card {
  max-height: 550px;
  max-width: 300px;
  height: 100%;
  width: 100%;
  border-radius: $border-rounding;
  box-shadow: $card-shadowing;
  border: $primary-border;
  backface-visibility: hidden;
  backdrop-filter: blur(5px);
  background: radial-gradient(at 50% 0%, rgba(0,0,0,0,), rgba(255,255,255,0.3));
  position: absolute;

  &:hover {
    box-shadow: $card-hover-shadowing;
    border: $secondary-border;
    cursor: pointer;

    .beerDetails {
      border-color: $secondary-color;

      h2 {
        color: $primary-text;
      }
    }
  }
}
