@import "../../variables.scss";


.navFlex {
  position: fixed;
  height: $NavBar-height-mb;
  width: 100vw;
  background-color: grey;
  opacity: 0.6;
  padding: 0 $base-spacing * 4;
  border-bottom: $primary-border;
  color: $primary-color;
  z-index: 2;
  display: flex;
  align-items: center;
  // justify-content: space-between;
  text-decoration: none;
  div {
    padding: 20px;
    &.searchPanel {
      font-size: 1.5em;
      display: flex;
    }
    &.checkBox{
      display: flex;
      justify-content: space-evenly;
      
      color: white;
    }
  }
}



@media only screen and (min-width: 768px) {
  nav {
    height: $NavBar-height;

    div {
      &.searchPanel {
        font-size: 2em;
      }

      img {
        height: 50px;
      }

      h1 {
        display: unset;
        font-size: 1.75em;
      }
    }
  }
}
